import React, { PureComponent } from 'react';
import Accordion from '../../../../Accordion';
import { formatMoneyDetailedLink } from '../../ui/formatMoneyDetailedLink';
import Header from '../Header';
import styles from './PropertyTraceability.module.scss';

type PropertyTraceabilityProps = {
  linkData: any[];
  index: number;
  disabled?: boolean;
};

type PropertyTraceabilityState = {
  ndsData: any;
};

const CustomDiv = (props: any) => {
  return <div>{props.children}</div>;
};

/**
 * Компонент Собственности
 */
class PropertyTraceability extends PureComponent<
  PropertyTraceabilityProps,
  PropertyTraceabilityState
> {
  constructor(props: PropertyTraceabilityProps) {
    super(props);

    this.state = {
      ndsData: [{}],
    };
  }

  render() {
    const {
      state: { ndsData },
      props: { linkData, index },
    } = this;

    const propertyInfo = linkData[index];

    const totalCost = propertyInfo.props_info.reduce(
      (result: number, item: { prop_cost: string }) =>
        result + (!isNaN(Number(item.prop_cost)) ? Number(item.prop_cost) : 0),
      0,
    );

    if (!ndsData.length) return null;

    return (
      <>
        <Header linkData={linkData} index={index} />
        <Accordion allowMultipleOpen new>
          {ndsData.map((_: any, index: number) => (
            <CustomDiv
              key={'property-tracebility-founders-item-' + index}
              label="Собственность"
              isOpen={index === 0}
              labelContainerClassName={styles.labelContainer}
              isArrowFront
            >
              <table className={styles.data_table} cellSpacing="0" cellPadding="0">
                <thead className={styles.data_table_header}>
                  <tr>
                    <td>Объект</td>
                    <td>Доля, %</td>
                    <td>Мощность / Площадь</td>
                    <td>Стоимость, ₽</td>
                  </tr>
                </thead>
                <tbody className={styles.data_table_tbody}>
                  {propertyInfo.props_info.map((item: any) => (
                    <tr>
                      <td>{item.prop_type}</td>
                      <td>{item.part}</td>
                      <td>{item.prop_power ? item.prop_power : item.prop_area}</td>
                      <td>{formatMoneyDetailedLink(item.prop_cost)}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
              <div className={styles.totalCost}>
                <span className={styles.label}>Общая стоимость, ₽:</span>
                {formatMoneyDetailedLink(totalCost)}
              </div>
            </CustomDiv>
          ))}
        </Accordion>
      </>
    );
  }
}

export default PropertyTraceability;
