import { inject, observer } from 'mobx-react';
import React, { PureComponent } from 'react';
import { CommonStoreType } from '../../../../../stores/common/commonStore';
import { GraphStoreType } from '../../../../../stores/graph/graphStore';
import Accordion from '../../../../Accordion';
import Icon from '../../../../Icon';
import { formatMoneyDetailedLink } from '../../ui/formatMoneyDetailedLink';
import Header from '../Header';
import { ReactComponent as MultipleIcon } from '../assets/multiple.svg';
import styles from './ASKNDS2.module.scss';
import './ASKNDS2.scss';

type ASK_NDS2Props = {
  disabled?: boolean;
  linkData: any[];
  index: number;
  commonStore?: CommonStoreType;
  graphStore?: GraphStoreType;
};

type ASK_NDS2State = {
  ndsData: any;
};

const CustomDiv = (props: any) => {
  return <div>{props.children}</div>;
};

/**
 * Компонент АСК НДС-2 связи
 */
@inject('commonStore', 'graphStore')
@observer
class ASK_NDS2 extends PureComponent<ASK_NDS2Props, ASK_NDS2State> {
  constructor(props: ASK_NDS2Props) {
    super(props);

    this.state = {
      ndsData: [{}],
    };
  }

  render() {
    const {
      props: { linkData, index },
      state: { ndsData },
    } = this;

    const information = linkData[index];

    if (!ndsData?.length) return null;

    return (
      <>
        <Header linkData={linkData} index={index} />
        <Accordion allowMultipleOpen new>
          {ndsData.map((_: any, index: number) => (
            <CustomDiv
              key={'ask-nds2-founders-item-' + index}
              className={styles.section}
              label={<span className={styles.header}>Параметры связи</span>}
              isOpen={index === 0}
              labelContainerClassName={styles.labelContainer}
              isArrowFront
            >
              {information.subtype_id === 45 || information.subtype_id === 49 ? (
                <div className={styles.askInfo}>
                  <div className={styles.askInfoItem}>
                    <span className={styles.title}>Сумма НДС, исчисленная, ₽: </span>
                    <span className={styles.option}>
                      {formatMoneyDetailedLink(information?.vat_calc) +
                        (information?.shareVatCalc
                          ? '(' + information?.shareVatCalc + '%' + ')'
                          : '')}
                    </span>
                  </div>
                  <div className={styles.askInfoItem}>
                    <span className={styles.title}>Сумма НДС, к вычету, ₽: </span>
                    <span className={styles.option}>
                      {formatMoneyDetailedLink(information?.vat_deduct) +
                        (information?.shareVatDeduct
                          ? '(' + information?.shareVatDeduct + '%' + ')'
                          : '')}
                    </span>
                  </div>
                  <div className={styles.askInfoItem}>
                    <span className={styles.title}>Сумма расхождений, ₽: </span>
                    <span className={styles.option}>
                      {formatMoneyDetailedLink(information.discrepancy_total)}
                      {information?.discrepancy_total ? (
                        <Icon name="flash" className={styles.data_table_tbody_gap} />
                      ) : (
                        ''
                      )}
                    </span>
                  </div>

                  <div className={`${styles.askInfoItem} ${styles.subitem}`}>
                    <span className={styles.title}>Сумма расхождений по 8 разделу НП, ₽: </span>
                    <span className={styles.option}>
                      {formatMoneyDetailedLink(information.gap8_total)}
                    </span>
                  </div>

                  <div className={`${styles.askInfoItem} ${styles.subitem}`}>
                    <span className={styles.title}>Сумма расхождений по 11 разделу НП, ₽: </span>
                    <span className={styles.option}>
                      {formatMoneyDetailedLink(information.gap11_total)}
                    </span>
                  </div>

                  <div className={`${styles.askInfoItem} ${styles.subitem}`}>
                    <span className={styles.title}>
                      Завышение вычета НДС между 8 разделом НП и записями о СФ контрагента, ₽:{' '}
                    </span>
                    <span className={styles.option}>
                      {formatMoneyDetailedLink(information.mis8_total)}
                    </span>
                  </div>

                  <div className={`${styles.askInfoItem} ${styles.subitem}`}>
                    <span className={styles.title}>
                      Завышение вычета НДС между 11 разделом НП и записями о СФ контрагента, ₽:{' '}
                    </span>
                    <span className={styles.option}>
                      {formatMoneyDetailedLink(information.mis11_total)}
                    </span>
                  </div>

                  <div className={styles.askInfoItem}>
                    <span className={styles.title}>
                      НДС по журналу учета выставленных сч/ф продавца, ₽:{' '}
                    </span>
                    <span className={styles.option}>
                      {information?.vat10total || information.vat10total === 0
                        ? formatMoneyDetailedLink(information.vat10total)
                        : '-'}
                    </span>
                  </div>

                  <div className={styles.askInfoItem}>
                    <span className={styles.title}>
                      НДС по журналу учета полученных сч/ф покупателя, ₽:{' '}
                    </span>
                    <span className={styles.option}>
                      {information?.vat11total || information.vat11total === 0
                        ? formatMoneyDetailedLink(information.vat11total)
                        : '-'}
                    </span>
                  </div>
                </div>
              ) : (
                /* Приход */
                <div className={styles.wrapper_creditDebit}>
                  <div className={styles.askInfo}>
                    <span className={styles.header}>Приход</span>
                    <div className={styles.askInfoItem}>
                      <span className={styles.title}>Всего, ₽: </span>
                      <span>{formatMoneyDetailedLink(information?.debit_sum)}</span>
                    </div>
                    {information.payment_details?.length > 0 && (
                      <>
                        <div className={styles.askInfoItem}>
                          <span className={styles.title}>Вид платежа:</span>
                          <span>Сумма</span>
                        </div>
                        {information.payment_details.map((element: any, index: number) => {
                          return (
                            element.debit && (
                              <div className={styles.askInfoItem}>
                                <span className={styles.title}>{`${element.payment_type}`}:</span>
                                <span>{formatMoneyDetailedLink(element.debit)}</span>
                              </div>
                            )
                          );
                        })}
                      </>
                    )}
                  </div>
                  {/* Расход */}
                  <div className={styles.askInfo}>
                    <span className={styles.header}>Расход</span>

                    <div className={styles.askInfoItem}>
                      <span className={styles.title}>Всего, ₽: </span>
                      <span>{formatMoneyDetailedLink(information?.credit_sum)}</span>
                    </div>

                    {information.payment_details?.length > 0 && (
                      <>
                        <div className={styles.askInfoItem}>
                          <span className={styles.title}>Вид платежа:</span>
                          <span>Сумма</span>
                        </div>
                        {information.payment_details.map((element: any, index: number) => {
                          return (
                            element.credit && (
                              <div className={styles.askInfoItem}>
                                <span className={styles.title}>{`${element.payment_type}`}:</span>
                                <span>{formatMoneyDetailedLink(element.credit)}</span>
                              </div>
                            )
                          );
                        })}
                      </>
                    )}
                  </div>
                </div>
              )}

              {information?.inter_quarter_periods?.length > 0 && (
                <div>
                  <div className={styles.data_container}>
                    <span className={styles.header}>
                      Суммы принятых вычетов НДС по периодам, в том числе отложенных
                    </span>
                  </div>
                  <div className={styles.data_container}>
                    <table className={styles.tablePeriod} cellSpacing="0" cellPadding="0">
                      <thead className={styles.tablePeriodHeader}>
                        <tr>
                          <td>Налоговый период</td>
                          <td>Сумма НДС исчисленная продавцом, руб</td>
                          <td>Сумма НДС к вычету у покупателя, руб</td>
                          <td>Сумма разрыва, руб</td>
                        </tr>
                      </thead>
                      <tbody className={styles.tablePeriodTbody}>
                        {information?.inter_quarter_periods.map((item: any) => (
                          <tr>
                            <td>{item.periodStr}</td>
                            <td>{formatMoneyDetailedLink(item.vatCalc)}</td>
                            <td>
                              <div className={styles.containerMultiple}>
                                {formatMoneyDetailedLink(item.vatDeduct)}
                                {item.periodStr !== information.link_period_add.periodStr ? (
                                  <MultipleIcon
                                    className={styles.multiple_icon}
                                    title={'Приняты отложенные вычеты'}
                                  />
                                ) : (
                                  <div className={styles.multiple_icon}></div>
                                )}
                              </div>
                            </td>
                            <td>{formatMoneyDetailedLink(item.gap)}</td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </div>
              )}
            </CustomDiv>
          ))}
        </Accordion>
      </>
    );
  }
}

export default ASK_NDS2;
