import React from 'react';
import styles from './Section.module.scss';
import { sectionPropTypes } from './Section.props';

import { StatusesIcons } from '../../Icon';
import { ReactComponent as Arrow } from 'assets/icons/arrow_round.svg';
import { THEME } from 'constants/theme';

type SectionProps = {
  children: any;
  className?: string;
  isOpen: boolean;
  label: any;
  onClick: (label: any) => void;
  status?: any;
  isCluster?: boolean;
  countLinks?: any;
  isActive?: boolean;
  checkboxClick: () => void;
  keyForOpen?: string;
  theme?: keyof typeof THEME;
  isInnerAccordion?: boolean;
  labelContainerClassName?: string;
  isArrowFront?: boolean;
  new?: boolean;
};

/**
 * Компонент отрисовывает секцию для аккордиона
 *
 * @param {any} children - компонент для открытого состояния
 * @param {boolean} isOpen - флаг, показывающий открыта ли секция
 * @param {string} label - текст для секции
 * @param {func} onClick - функция, вызывающаяся при клике на секцию
 * @param {any} status - статус для истории изменения
 * @param {any} theme - тема интерфейса
 */
const Section = (props: SectionProps) => {
  const {
    onClick,
    isOpen,
    label,
    status,
    isCluster,
    countLinks,
    isActive,
    checkboxClick,
    keyForOpen,
    labelContainerClassName,
    isArrowFront,
    className,
  } = props;

  return (
    <section className={`${styles.wrapper} ${className}`} data-new={props.new}>
      <div className={`${styles.container}`}>
        <div
          className={`${styles.labelContainer} ${labelContainerClassName}`}
          onClick={() => onClick(keyForOpen)}
        >
          {isArrowFront && (
            <Arrow
              className={`${styles.arrow} ${isOpen && styles.arrow_down} arrow_front`}
            />
          )}
          <span className={styles.label}>{label}</span>
          <StatusesIcons statuses={status} />
          {!isArrowFront && (
            <Arrow
              className={`${styles.arrow} ${isOpen && styles.arrow_down}`}
            />
          )}
        </div>
        {isCluster && (
          <div className={styles.countLinks}>
            <span>
              Связей: <span className={styles.bold}>{countLinks}</span>
            </span>
            <div className={styles.checkboxContainer}>
              <div className={styles.checkbox} onClick={() => checkboxClick()}>
                {isActive && <div className={styles.active} />}
              </div>
            </div>
          </div>
        )}
      </div>
      {isOpen && <div>{props.children}</div>}
    </section>
  );
};

Section.propTypes = sectionPropTypes;

export default Section;
