import { inject, observer } from 'mobx-react';
import React, { PureComponent } from 'react';
import { GraphStoreType } from '../../../../../stores/graph/graphStore';
import Accordion from '../../../../Accordion';
import { formatMoneyDetailedLink } from '../../ui/formatMoneyDetailedLink';
import Header from '../Header';
import { ReactComponent as MultipleIcon } from '../assets/multiple.svg';
import Criteria from './Criteria';
import styles from './TypologyLink.module.scss';

type TypologyLinkProps = {
  graphStore?: GraphStoreType;
  linkData: any[];
  index: number;
  disabled: boolean;
  theme: any;
};

type TypologyLinkState = {
  links: any;
};

const CustomDiv = (props: any) => {
  return <div>{props.children}</div>;
};

/**
 * Компонент Отображение данных связи схемы Типологии
 */
@inject('graphStore')
@observer
class TypologyLink extends PureComponent<TypologyLinkProps, TypologyLinkState> {
  constructor(props: TypologyLinkProps) {
    super(props);

    this.state = {
      links: [{}],
    };
  }

  render() {
    const {
      props: { linkData, index, disabled },
      state: { links },
    } = this;

    const linkDataAll = linkData[index];
    const information = linkDataAll.typology_link;

    return (
      <>
        <Header linkData={linkData} index={index} />
        <Accordion allowMultipleOpen new>
          {links.map((_: any, index: number) => (
            <CustomDiv
              label="Схема"
              key={'typology-link-' + index}
              labelContainerClassName={styles.labelContainer}
              isArrowFront
              isOpen
            >
              <div className={styles.dropdownContainerDiv}>
                <div className={styles.typologyInfoItem}>
                  <span className={styles.title}>Тип связи:</span>
                  <span className={styles.option}>
                    {information.linkTypeName
                      ? information.linkTypeName.toLowerCase()
                      : 'нет данных'}
                  </span>
                </div>
                <div className={styles.typologyInfoItem}>
                  <span className={styles.title}>Направленность:</span>
                  <span className={styles.option}>
                    {information.directed === true ? 'да' : 'нет'}
                  </span>
                </div>
                <div className={styles.typologyInfoItem}>
                  <span className={styles.title}>Наименование участника схемы, исходящий:</span>
                  <span className={styles.option}>
                    {information.participantShortName1
                      ? information.participantShortName1
                      : 'нет данных'}
                  </span>
                </div>
                <div className={styles.typologyInfoItem}>
                  <span className={styles.title}>Наименование участника схемы, входящий:</span>
                  <span className={styles.option}>
                    {information.participantShortName2
                      ? information.participantShortName2
                      : 'нет данных'}
                  </span>
                </div>
                {information.criterias && (
                  <Accordion new>
                    <CustomDiv
                      label="Критерии связи в схеме"
                      labelContainerClassName={styles.labelContainer}
                      isArrowFront
                      isOpen
                    >
                      <ul className={styles.criteria}>
                        {information.criterias?.map((criteria: any, i: number) => (
                          <li key={`${criteria?.name}-${i}`}>
                            <Criteria criteria={criteria} />
                          </li>
                        ))}
                      </ul>
                    </CustomDiv>
                  </Accordion>
                )}
                <div className={styles.typologyInfoItem}>
                  <span className={styles.title}>Процент отклонения по кооперации, %:</span>
                  <span className={styles.option}>{information.deviation_percent || 0}</span>
                </div>
                {linkDataAll?.inter_quarter_periods?.length > 0 && (
                  <div>
                    <div className={styles.container}>
                      <span className={styles.titleMultiple}>
                        Суммы принятых вычетов НДС по периодам, в том числе отложенных
                      </span>
                    </div>
                    <div className={styles.container}>
                      <table className={styles.tablePeriod} cellSpacing="0" cellPadding="0">
                        <thead className={styles.tablePeriodHeader}>
                          <tr>
                            <td>Налоговый период</td>
                            <td>Сумма НДС исчисленная продавцом, руб</td>
                            <td>Сумма НДС к вычету у покупателя, руб</td>
                            <td>Сумма разрыва, руб</td>
                          </tr>
                        </thead>
                        <tbody className={styles.tablePeriodTbody}>
                          {linkDataAll?.inter_quarter_periods.map((item: any) => (
                            <tr>
                              <td>{item.periodStr}</td>
                              <td>{formatMoneyDetailedLink(item.vatCalc)}</td>
                              <td>
                                <div className={styles.containerMultiple}>
                                  {formatMoneyDetailedLink(item.vatDeduct)}
                                  {item.periodStr !== linkDataAll.link_period_add.periodStr ? (
                                    <MultipleIcon
                                      className={styles.multiple_icon}
                                      title={'Приняты отложенные вычеты'}
                                    />
                                  ) : (
                                    <div className={styles.multiple_icon}></div>
                                  )}
                                </div>
                              </td>
                              <td>{formatMoneyDetailedLink(item.gap)}</td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                  </div>
                )}
              </div>
            </CustomDiv>
          ))}
        </Accordion>
      </>
    );
  }
}

export default TypologyLink;
