import Button from 'components/Button';
import { StatusesIcons } from 'components/Icon';
import { inject, observer } from 'mobx-react';
import React, { PureComponent, ReactNode } from 'react';
import { CommonStoreType } from '../../../stores/common/commonStore';
import { GraphStoreType } from '../../../stores/graph/graphStore';
import Accordion from '../../Accordion';
import { ReactComponent as FileIcon } from '../../FileUploader/assets/file.svg';
import { Modal, ModalBody, ModalFooter, ModalHeader } from '../Modal';
import styles from './DetailedLinkNDS.module.scss';
import ASK_NDS2 from './components/ASK_NDS2';
import ChangeHistory from './components/ChangeHistory';
import Contract from './components/Contract';
import MarkInfo from './components/MarkInfo';
import ProductTracebility from './components/ProductTraceability';
import PropertyTraceability from './components/PropertyTraceability';
import Rent from './components/Rent';
import TypologyLink from './components/TypologyLink';
import { ReactComponent as LineIcon } from './components/assets/line.svg';

interface DetailedLinkNDSProps {
  graphStore?: GraphStoreType;
  commonStore?: CommonStoreType;
  [key: string]: any;
}

const CustomDiv = (props: any) => {
  return <div>{props.children}</div>;
};

/**
 * Основной компонент для детализации связи (Карточка связи для потоков НДС)
 */
@inject('graphStore', 'commonStore')
@observer
class DetailedLinkNDS extends PureComponent<DetailedLinkNDSProps> {
  async componentDidMount() {
    const { linkID, graphStore } = this.props;
    await graphStore!.fetchDetailedLink(linkID);
  }

  /**
   * Метод отрисовывает список файлов
   */
  _renderFileList = (information: any): ReactNode => {
    const { graphStore } = this.props;
    const files = information.attach_documents;

    return files.map((file: any) => (
      <div
        key={'file-list-item-' + file.file_name}
        className={styles.fileItem}
        onClick={() => graphStore!.getUploadedDocument(file.document_id, file.file_name)}
      >
        <FileIcon className={styles.fileIcon} />
        <span className={styles.fileItemLabel}>{file.file_name}</span>
      </div>
    ));
  };

  handleReasonValue = (id?: string) => {
    const { invalidReasonsList } = this.props.commonStore!;

    if (id) {
      const value = invalidReasonsList!.find((reason: any) => reason.reason_id === id);
      return value!.name;
    } else {
      return '';
    }
  };

  handleChangeValue = (id?: string) => {
    const { changeReasonsList } = this.props.commonStore!;

    if (id) {
      const value = changeReasonsList!.find((reason: any) => reason.reason_id === id);
      return value!.name;
    } else {
      return '';
    }
  };

  render() {
    const { onClose, graphStore, commonStore } = this.props;

    const linkData = this.props.graphStore!.GraphDataStore.linkDetailedData;
    const statuses = linkData?.map((data) => data.change_status);

    if (!linkData) {
      return null;
    }

    const typeName = (value: string) => (value ? `${value}, ` : '');

    return (
      <Modal>
        <ModalHeader onClose={onClose}>
          <div className={styles.header}>
            <span>Детализация связи</span>
            {!graphStore!.pending ? (
              <StatusesIcons statuses={[...new Set(statuses)] || []} />
            ) : null}
          </div>
        </ModalHeader>
        <ModalBody withFooter>
          <div className={styles.container}>
            {!graphStore!.pending && (
              <Accordion allowMultipleOpen new>
                {linkData?.map((information: any, index: number) => (
                  <CustomDiv
                    key={'consistency-founders-item-' + index}
                    className={`${styles.section}`}
                    label={
                      <div className={styles.title}>
                        <span className={styles.subtypeName}>
                          {information.user_linked
                            ? `Пользовательская связь, ${typeName(
                                information.type_name,
                              ).toLowerCase()} ${information.subtype_name?.toLowerCase()}`
                            : information.subtype_name}
                          <StatusesIcons statuses={[information.change_status]} />
                        </span>
                        <div className={styles.containerMainInfo}>
                          <div className={styles.lineIcon}>
                            <LineIcon />
                          </div>
                          <div className={styles.containerName}>
                            <div className={styles.headerName}>
                              <div className={styles.headerName__prefix}>{information.inn1}</div>
                              {information.short_name1
                                ? information.short_name1
                                : '(Наименование не определено)'}
                            </div>
                            <div className={styles.headerName}>
                              <div className={styles.headerName__prefix}>{information.inn2}</div>
                              {information.short_name2
                                ? information.short_name2
                                : '(Наименование не определено)'}
                            </div>
                          </div>
                        </div>
                      </div>
                    }
                    isOpen={index === 0}
                    labelContainerClassName={styles.accordionOuter}
                    isArrowFront
                  >
                    <div className={styles.containerDiv}>
                      {information.typology_link && (
                        <TypologyLink
                          linkData={linkData}
                          index={index}
                          theme={commonStore?.appTheme}
                          disabled
                        />
                      )}
                      {/* АСК НДС-2 */}
                      {[45, 49, 50].includes(information.subtype_id) && (
                        <ASK_NDS2 linkData={linkData} index={index} disabled />
                      )}
                      {/* Пользовательская связь */}
                      {information.user_linked && (
                        <Rent linkData={linkData} index={index} disabled />
                      )}
                      {/* Прослеживаемость */}
                      {information.traces_info?.length > 0 && (
                        <ProductTracebility linkData={linkData} index={index} />
                      )}
                      {/* Собственность */}
                      {information.props_info?.length > 0 && (
                        <PropertyTraceability linkData={linkData} index={index} />
                      )}
                      {/* Маркировка товара */}
                      {information.mark_info?.length > 0 && (
                        <MarkInfo linkData={linkData} index={index} />
                      )}
                      {/* Госконтракты */}
                      {information.contract_info?.length > 0 && (
                        <Contract linkData={linkData} index={index} disabled />
                      )}
                      {information.attach_documents?.length > 0 && (
                        <div className={styles.fileContainer}>
                          <div className={styles.data_label}>Прикрепленные файлы</div>
                          <div className={styles.fileList}>{this._renderFileList(information)}</div>
                        </div>
                      )}
                      <ChangeHistory
                        label="История изменений"
                        data={information.change_histories}
                        index={index}
                      />
                    </div>
                  </CustomDiv>
                ))}
              </Accordion>
            )}
          </div>
        </ModalBody>
        <ModalFooter>
          <div className={styles.footer}>
            <Button mode="secondary" onClick={onClose}>
              Закрыть
            </Button>
          </div>
        </ModalFooter>
      </Modal>
    );
  }
}

export default DetailedLinkNDS;
