import { ReactComponent as Calendar } from 'assets/icons/calendar.svg';
import EditLink from 'components/modals/EditLink';
import React, { PureComponent } from 'react';
import styles from './Header.module.scss';

type HeaderProps = {
  linkData: any[];
  index: number;
};

class Header extends PureComponent<HeaderProps> {
  render() {
    const {
      props: { linkData, index },
    } = this;

    const information = linkData[index];

    return (
      <header className={styles.header}>
        <div className={styles.period}>
          {information.link_period_add && (
            <>
              {information.link_period_add.quarter} кв. {information.link_period_add.year}
              <Calendar className={styles.icon_calendar} />
            </>
          )}
        </div>
        <EditLink linkData={linkData} index={index} />
      </header>
    );
  }
}

export default Header;
