/**
 * Форматирование денежной суммы для детализации связи
 */
export const formatMoneyDetailedLink = (amount: any, currency: string = 'RUB') => {
  if (typeof +amount !== 'number' || isNaN(+amount)) {
    return '-';
  }

  if (amount == 0) {
    return 0;
  } else {
    const numberFormat = new Intl.NumberFormat('ru-RU', {
      style: 'currency',
      maximumFractionDigits: 0,
      minimumFractionDigits: 0,
      currency,
    });
    const str = numberFormat.format(amount);
    return numberFormat.format(amount).slice(0, str.length - 1);
  }
};
