import React, { Fragment, PureComponent } from 'react';
import { formatMoneyDetailedNode } from '../../../../../../utils/ui/formatMoneyDetailedNode';
import styles from './Criteria.module.scss';

type CriteriaProps = {
  criteria: any;
};

/**
 * Компонент Критерии
 */
class Criteria extends PureComponent<CriteriaProps> {
  render() {
    const {
      props: { criteria },
    } = this;

    if (!criteria.value) {
      return null;
    }

    return (
      <div className={styles.container}>
        <h4>{criteria.name || ''}</h4>
        <div className={styles.value}>Критерий сработал: {criteria.value ? 'да' : 'нет'}</div>
        {criteria.parameters && (
          <div className={styles.params}>
            <span>Параметры и значения параметров критерия</span>
            <dl>
              {criteria.parameters?.map((parameter: any) => (
                <Fragment key={parameter.name}>
                  <dt>{parameter.name}</dt>
                  <dd>
                    {parameter.value
                      ? parameter.value === 'true'
                        ? 'Да'
                        : parameter.value === 'false'
                        ? 'Нет'
                        : parameter.valueType === 'FLOAT'
                        ? formatMoneyDetailedNode(parameter.value, true)
                        : parameter.value
                      : 'нет данных'}
                  </dd>
                </Fragment>
              ))}
            </dl>
          </div>
        )}
      </div>
    );
  }
}

export default Criteria;
