import { inject, observer } from 'mobx-react';
import React, { PureComponent } from 'react';
import { GraphStoreType } from '../../../../../stores/graph/graphStore';
import { formatDate } from '../../../../../utils/ui/formatDateDetailedLink';
import Accordion from '../../../../Accordion';
import { formatMoneyDetailedLink } from '../../ui/formatMoneyDetailedLink';
import Header from '../Header';
import styles from './Contract.module.scss';

type ContractProps = {
  disabled?: boolean;
  graphStore?: GraphStoreType;
  linkData: any[];
  index: number;
};

type ContractState = {
  payersData: any;
};

const CustomDiv = (props: any) => {
  return <div>{props.children}</div>;
};

/**
 * Компонент Госконтракты
 */
@inject('graphStore')
@observer
class Contract extends PureComponent<ContractProps, ContractState> {
  constructor(props: ContractProps) {
    super(props);

    this.state = {
      payersData: [{}],
    };
  }

  render() {
    const {
      props: { linkData, index },
      state: { payersData },
    } = this;

    const information = linkData[index];

    if (!payersData?.length) return null;

    return (
      <>
        <Header linkData={linkData} index={index} />
        <div className={styles.contractInfo}>
          <span className={styles.header}>Сумма по контрактам</span>
          <div className={styles.data_container}>
            <span className={`${styles.title} ${styles.title__offset}`}>Сумма, ₽: </span>
            <span className={styles.titleDetails}>
              {formatMoneyDetailedLink(information.contract_price)}
            </span>
          </div>
        </div>
        <div className={styles.data_container}>
          {information.contract_info?.length > 0 && (
            <Accordion allowMultipleOpen new>
              {information.contract_info.map((contract: any, index: number) => (
                <CustomDiv
                  key={'contract-list-item-' + index}
                  label={<span>{'Контракт ' + (index + 1)}</span>}
                  isOpen={index === 0}
                  isInnerAccordion
                  isArrowFront
                  labelContainerClassName={styles.labelContainer}
                >
                  <div className={styles.contractInfo}>
                    <span className={styles.header}>Сведения о контракте</span>
                    <div className={styles.contractInfoItem}>
                      <span className={styles.title}>Реестровый номер контракта: </span>
                      <span>{contract.contract_number || '-'}</span>
                    </div>
                    <div className={styles.contractInfoItem}>
                      <span className={styles.title}>Сумма контракта, ₽: </span>
                      <span>{formatMoneyDetailedLink(contract.total_price)}</span>
                    </div>
                    <div className={styles.contractInfoItem}>
                      <span className={styles.title}>Дата начала работ: </span>
                      <span>{formatDate(contract.start_date)}</span>
                    </div>
                    <div className={styles.contractInfoItem}>
                      <span className={styles.title}>Дата завершения работ: </span>
                      <span>{formatDate(contract.end_date)}</span>
                    </div>
                    <div className={styles.contractInfoItem}>
                      <span className={styles.title}>
                        Код / наименование федерального проекта:{' '}
                      </span>
                      <span>
                        {contract.federal_project_code || '-'} /{' '}
                        {contract.federal_project_name || '-'}
                      </span>
                    </div>
                    <div className={styles.contractInfoItem}>
                      <span className={styles.title}>
                        Код / наименование национального проекта:{' '}
                      </span>
                      <span>
                        {contract.nartional_project_code || '-'} /{' '}
                        {contract.national_project_code || '-'}
                      </span>
                    </div>
                  </div>
                  {contract.payment_months?.length > 0 && (
                    <div className={styles.contractInfo}>
                      <span className={styles.header}>
                        {'График платежей по контракту, ₽ (' +
                          contract.payment_months?.length +
                          ')'}
                      </span>
                      {contract.payment_months?.map((month: any, index: number) => (
                        <div className={styles.contractInfoItem}>
                          <span className={`${styles.title} ${styles.money}`}>
                            {(month < 10 ? '0' + month : month) + '.' + contract.payment_year}
                          </span>
                          <span className={styles.option}>
                            {formatMoneyDetailedLink(contract.payment_sums[index])}
                          </span>
                        </div>
                      ))}
                    </div>
                  )}
                </CustomDiv>
              ))}
            </Accordion>
          )}
        </div>
      </>
    );
  }
}

export default Contract;
