import { inject, observer } from 'mobx-react';
import React, { PureComponent } from 'react';
import { ReactComponent as Person } from '../../../../../assets/icons/person.svg';
import { GraphStoreType } from '../../../../../stores/graph/graphStore';
import { formatDateDetailedLink } from '../../../../../utils/ui/formatDateDetailedLink';
import Accordion from '../../../../Accordion';
import { formatMoneyDetailedLink } from '../../ui/formatMoneyDetailedLink';
import Header from '../Header';
import styles from './Rent.module.scss';

type RentProps = {
  disabled?: boolean;
  graphStore?: GraphStoreType;
  linkData: any[];
  index: number;
};

type RentState = {
  payersData: any;
  nameTitle: string;
};

const CustomDiv = (props: any) => {
  return <div>{props.children}</div>;
};

/**
 * Компонент Аренда связи
 */
@inject('graphStore')
@observer
class Rent extends PureComponent<RentProps, RentState> {
  constructor(props: RentProps) {
    super(props);

    this.state = {
      payersData: [{}],
      nameTitle: '',
    };
  }

  async componentDidMount() {
    const { graphStore, linkData, index } = this.props;

    const information = linkData[index];

    // Получение типов связи
    const edgeTypesData = graphStore!.edgeTypesData;
    let cooperationNameTitle = '';
    let consistencyNameTitle = '';

    if (information?.type_id === 1) {
      if (edgeTypesData) {
        const subtypesCooperation = edgeTypesData[0]?.subtypes;

        for (let i = 0; i < subtypesCooperation.length; i++) {
          if (information?.subtype_id === subtypesCooperation[i].type_id) {
            cooperationNameTitle =
              subtypesCooperation[i].name ||
              subtypesCooperation[i].subtype_name ||
              'Неизвестный тип связи';
            break;
          }
        }
      }

      this.setState({ nameTitle: cooperationNameTitle });
    } else {
      if (edgeTypesData) {
        const subtypesConsistency = edgeTypesData[1]?.subtypes;

        for (let i = 0; i < subtypesConsistency.length; i++) {
          if (information?.subtype_id === subtypesConsistency[i].type_id) {
            consistencyNameTitle =
              subtypesConsistency[i].name ||
              subtypesConsistency[i].subtype_name ||
              'Неизвестный тип связи';
            break;
          }
        }
      }

      this.setState({ nameTitle: consistencyNameTitle });
    }
  }

  render() {
    const {
      props: { linkData, index },
      state: { payersData },
    } = this;

    const information = linkData[index];

    return (
      <div>
        <Header linkData={linkData} index={index} />
        {payersData?.length > 0 && (
          <Accordion allowMultipleOpen new>
            {payersData.map((_: any, index: number) => (
              <CustomDiv
                key={'rent-founders-item-' + index}
                label={information.subtype_name}
                isOpen={index === 0}
                labelContainerClassName={styles.labelContainer}
                isArrowFront
              >
                <div className={styles.data_container}>
                  <table className={styles.data_table} cellSpacing="0" cellPadding="0">
                    <thead className={styles.data_table_header}>
                      <tr>
                        <td>Параметр связи</td>
                        <td>Значение параметра</td>
                      </tr>
                    </thead>
                    <tbody className={styles.data_table_tbody}>
                      <tr>
                        <td>Сумма, ₽</td>
                        <td>
                          {information?.value == null
                            ? formatMoneyDetailedLink(Number(information?.value))
                            : formatMoneyDetailedLink(Number(information?.value.replace(',', '.')))}
                        </td>
                      </tr>
                      <tr>
                        <td>Автор связи</td>
                        <td>
                          <div className={styles.user}>
                            <Person className={styles.icon_person} />
                            {information?.created_by}
                          </div>
                        </td>
                      </tr>
                      <tr>
                        <td>Дата создания связи</td>
                        <td>{formatDateDetailedLink(information?.created)}</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </CustomDiv>
            ))}
          </Accordion>
        )}
      </div>
    );
  }
}

export default Rent;
