import React, { PureComponent } from 'react';

import { ReactComponent as Person } from 'assets/icons/person.svg';
import Accordion from 'components/Accordion';
import { formatDateDetailedLink } from 'utils/ui/formatDateDetailedLink';

import styles from './ChangeHistory.module.scss';

interface IHistory {
  change_date: Date;
  change_type: 'N' | 'K' | 'U' | 'I';
  change_text: string;
  user_name: string;
}

interface IChangeHistoryProps {
  index: number;
  data: IHistory[];
  label: string;
}

const CustomDiv = (props: any) => {
  return <div>{props.children}</div>;
};

class ChangeHistory extends PureComponent<IChangeHistoryProps> {
  render() {
    const { index, data, label } = this.props;

    return (
      <>
        <Accordion allowMultipleOpen new>
          <CustomDiv
            key={'change-history-' + index}
            label={<span className={styles.title}>{label}</span>}
            isOpen
            labelContainerClassName={styles.labelContainer}
            isArrowFront
          >
            <div className={styles.data_container}>
              <table className={styles.data_table} cellSpacing="0" cellPadding="0">
                <thead className={styles.data_table_header}>
                  <tr>
                    <th>Дата и время</th>
                    <th>Кем изменено</th>
                    <th>Изменение</th>
                  </tr>
                </thead>
                <tbody className={styles.data_table_tbody}>
                  {data.map((history) => (
                    <tr>
                      <td>{formatDateDetailedLink(new Date(history.change_date).toISOString())}</td>
                      <td>
                        <div className={styles.user}>
                          <Person className={styles.icon_person} />
                          {history.user_name}
                        </div>
                      </td>
                      <td>{history.change_text}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </CustomDiv>
        </Accordion>
      </>
    );
  }
}

export default ChangeHistory;
