import React, { PureComponent } from 'react';
import groupByCategory from 'utils/ui/groupByCategory';
import Accordion from '../../../../Accordion';
import { formatMoneyDetailedLink } from '../../ui/formatMoneyDetailedLink';
import Header from '../Header';
import styles from './MarkInfo.module.scss';

type MarkInfoProps = {
  linkData: any[];
  index: number;
  disabled?: boolean;
};

type MarkInfoState = {
  ndsData: any;
};

const CustomDiv = (props: any) => {
  return <div>{props.children}</div>;
};

/**
 * Компонент Маркировка товара
 */
class MarkInfo extends PureComponent<MarkInfoProps, MarkInfoState> {
  constructor(props: MarkInfoProps) {
    super(props);

    this.state = {
      ndsData: [{}],
    };
  }

  render() {
    const {
      state: { ndsData },
      props: { linkData, index },
    } = this;

    const marksInfo = linkData[index];

    const groups = groupByCategory(marksInfo?.mark_info);

    if (!ndsData.length) return null;

    return (
      <>
        <Header linkData={linkData} index={index} />
        <Accordion allowMultipleOpen new>
          {ndsData.map((_: any, index: number) => (
            <CustomDiv
              key={'product-tracebility-founders-item-' + index}
              label="Маркировка товаров"
              isOpen={index === 0}
              labelContainerClassName={styles.labelContainer}
              isArrowFront
            >
              <div>
                <table className={styles.data_table} cellSpacing="0" cellPadding="0">
                  <thead className={styles.data_table_header}>
                    <tr>
                      <td>Товарная группа</td>
                      <td>Упаковка</td>
                      <td data-number>Количество</td>
                      <td data-number>Стоимость, ₽</td>
                      <td data-number>НДС, ₽</td>
                      <td data-number>Акциз, ₽</td>
                    </tr>
                  </thead>
                  <tbody className={styles.data_table_tbody}>
                    {groups.map((item: any, index: number) => (
                      <tr key={item.goods_group + index}>
                        <td>{item.goods_group}</td>
                        <td>{item.package_type}</td>
                        <td data-number>{item.count.toLocaleString('ru-RU')}</td>
                        <td data-number>{formatMoneyDetailedLink(item.cost)}</td>
                        <td data-number>{formatMoneyDetailedLink(item.nds)}</td>
                        <td data-number>{formatMoneyDetailedLink(item.excise)}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </CustomDiv>
          ))}
        </Accordion>
      </>
    );
  }
}

export default MarkInfo;
