import React, { PureComponent } from 'react';
import styles from './Accordion.module.scss';
import { accordionPropTypes } from './Accordion.props';

import { CommonStoreType } from 'StoreTypes';
import { inject, observer } from 'mobx-react';
import Section from './Section';

type AccordionProps = {
  children: any;
  allowMultipleOpen?: boolean;
  commonStore?: CommonStoreType;
  new?: boolean;
};

type AccordionState = {
  openSections: {
    [key: string]: boolean;
  };
};

/**
 * Компонент аккордиона
 *
 * @param {any} children - компоненты аккордиона
 * @param {boolean} allowMultipleOpen - флаг, показывающий можно ли раскрывать несколько
 * секций одновременно
 */
@inject('commonStore')
@observer
class Accordion extends PureComponent<AccordionProps, AccordionState> {
  static propTypes = accordionPropTypes;

  static defaultProps = {
    allowMultipleOpen: false,
  };

  /**
   * Конструктор класса.
   *
   * Инициализирует открытые секции.
   */
  constructor(props: AccordionProps) {
    super(props);

    const openSections: { [key: string]: boolean } = {};

    let children = this.props.children;

    if (!Array.isArray(children)) {
      children = [children];
    }

    children.forEach((child: any, index: number) => {
      if (child.props.isOpen) {
        openSections[child.props.label + '-' + index] = true;
      }
    });

    this.state = {
      openSections,
    };
  }

  /**
   * Метод вызывающийся при клике на секцию
   *
   * @param {string} label - ключ, для поиска нужной секции
   */
  _onClick = (label: string): void => {
    const {
      props: { allowMultipleOpen },
      state: { openSections },
    } = this;
    const isOpen = !!openSections[label];

    if (allowMultipleOpen) {
      this.setState({
        openSections: {
          ...openSections,
          [label]: !isOpen,
        },
      });
    } else {
      this.setState({
        openSections: {
          [label]: !isOpen,
        },
      });
    }
  };

  render() {
    let {
      props: { children, commonStore },
      state: { openSections },
    } = this;

    if (!Array.isArray(children)) {
      children = [children];
    }

    return (
      <div className={styles.accordion}>
        {children.map((child: any, index: number) => (
          <Section
            key={'accordion-item-' + index}
            className={child.props.className}
            isOpen={!!openSections[child.props.label + '-' + index]}
            label={child.props.label}
            onClick={this._onClick}
            status={child.props.status}
            isCluster={child.props.isCluster}
            countLinks={child.props.countLinks}
            isActive={child.props.isActive}
            checkboxClick={() => child.props.checkboxClick()}
            keyForOpen={child.props.label + '-' + index}
            theme={commonStore?.appTheme}
            labelContainerClassName={child.props.labelContainerClassName}
            isArrowFront={child.props.isArrowFront}
            new={this.props.new}
          >
            {child.props.children}
          </Section>
        ))}
      </div>
    );
  }
}

export default Accordion;
