import React, { PureComponent } from 'react';
import Accordion from '../../../../Accordion';
import { formatMoneyDetailedLink } from '../../ui/formatMoneyDetailedLink';
import Header from '../Header';
import styles from './ProductTraceability.module.scss';

type ProductTracebilityProps = {
  linkData: any[];
  index: number;
  disabled?: boolean;
};

type ProductTracebilityState = {
  ndsData: any;
};

const CustomDiv = (props: any) => {
  return <div>{props.children}</div>;
};

/**
 * Компонент Прослеживаемости товаров связи
 */
class ProductTracebility extends PureComponent<ProductTracebilityProps, ProductTracebilityState> {
  constructor(props: ProductTracebilityProps) {
    super(props);

    this.state = {
      ndsData: [{}],
    };
  }

  render() {
    const {
      state: { ndsData },
      props: { linkData, index },
    } = this;

    const tracesInfo = linkData[index];

    const totalCost = tracesInfo.traces_info.reduce(
      (result: number, item: { cost: string }) =>
        result + (!isNaN(Number(item.cost)) ? Number(item.cost) : 0),
      0,
    );

    if (!ndsData.length) return null;

    return (
      <>
        <Header linkData={linkData} index={index} />
        <Accordion allowMultipleOpen new>
          {ndsData.map((_: any, index: number) => (
            <CustomDiv
              key={'product-tracebility-founders-item-' + index}
              label="Прослеживаемость товаров"
              isOpen={index === 0}
              labelContainerClassName={styles.labelContainer}
              isArrowFront
            >
              <table className={styles.data_table} cellSpacing="0" cellPadding="0">
                <thead className={styles.data_table_header}>
                  <tr>
                    <td>Группа товаров</td>
                    <td>Кол-во товаров</td>
                    <td>Единицы измерения</td>
                    <td>Сумма, ₽</td>
                  </tr>
                </thead>
                <tbody className={styles.data_table_tbody}>
                  {tracesInfo.traces_info.map((item: any) => (
                    <tr>
                      <td>{item.tnVedGroup}</td>
                      <td>{Number(item.quantity).toLocaleString('ru-RU')}</td>
                      <td>{item.unitName}</td>
                      <td>{formatMoneyDetailedLink(item.cost)}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
              <div className={styles.totalCost}>
                <span className={styles.label}>Общая стоимость, ₽:</span>
                {formatMoneyDetailedLink(totalCost)}
              </div>
            </CustomDiv>
          ))}
        </Accordion>
      </>
    );
  }
}

export default ProductTracebility;
