export default function groupByCategory(data: Record<string, unknown>[]) {
  const groupByCategory = (data || []).reduce((acc: any, item: any) => {
    const { goods_group } = item;
    acc[goods_group] = acc[goods_group] ?? [];
    acc[goods_group].push(item);
    return acc;
  }, {});

  let groups: any[] = [];

  Object.keys(groupByCategory).forEach((key) => {
    const groupsArr = groupByCategory[key];

    const group = groupsArr.reduce(
      (acc: any, item: any) => {
        return {
          ...acc,
          cost: acc.cost + item.cost,
          count: acc.count + item.count,
          excise: acc.excise + item.excise,
          nds: acc.nds + item.nds,
        };
      },
      {
        cost: 0,
        count: 0,
        excise: 0,
        goods_group: groupsArr[0].goods_group,
        nds: 0,
        package_type: groupsArr[0].package_type,
      },
    );

    groups.push(group);
  });

  return groups;
}
